<template>
    <div id="app">
        <Header></Header>
        <!-- 频道栏 -->
        <Breadcrumb name="物品百科" slug="item" root="/item" :publishEnable="true" :feedbackEnable="true" :adminEnable="false">
            <svg xmlns="http://www.w3.org/2000/svg" width="512" height="512" viewBox="0 0 512 512" v-bind:slot="'logo'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M382.5 109.2C345.1 71.8 296.3 50 243.9 47v110.9c46.7 19.2 79.6 65.2 79.6 118.7 0 58-47.2 105.2-105.2 105.2-47.8 0-86.6-38.9-86.6-86.6 0-39.6 32.2-71.8 71.8-71.8 33.1 0 59.9 26.9 59.9 59.9h-25c0-19.3-15.7-34.9-34.9-34.9-25.8 0-46.8 21-46.8 46.8 0 34 27.6 61.6 61.6 61.6 44.2 0 80.1-35.9 80.1-80.1 0-57-46.3-103.3-103.3-103.3C122.3 173.3 63 232.6 63 305.6 63 398.4 138.5 474 231.4 474c57.1 0 110.7-22.2 151.1-62.6 40.4-40.4 62.6-94 62.6-151.1s-22.2-110.7-62.6-151.1z"/></svg>
            <ItemBreadcrumb />
        </Breadcrumb>
        <!-- 左侧菜单 -->
        <LeftSidebar>
            <Sidebar :sidebar="$store.state.sidebar" />
        </LeftSidebar>
        <Main :withoutRight="false">
            <Search/>
            <!-- 路由页面内容 -->
            <router-view/>
            <!-- 右侧咨询 -->
            <RightSidebar>
                <Extend/>
            </RightSidebar>
            <Footer></Footer>
        </Main>
    </div>
</template>

<script>
    import ItemBreadcrumb from "@/components/ItemBreadcrumb.vue";
    import Sidebar from '@/components/Sidebar.vue';
    import Search from '@/components/Search.vue';
    import Extend from '@/components/Extend.vue';

    export default {
        name: 'App',
        props: [],
        data: function () {
            return {}
        },
        computed: {},
        methods: {},
        mounted: function () {
        },
        components: {
            ItemBreadcrumb,
            Sidebar,
            Search,
            Extend,
        },
        watch: {
            $route: {
                immediate: true,
                handler() {
                    if (
                        typeof this.$route.params.AucGenre === 'undefined' &&
                        typeof this.$route.params.AucSubTypeID === 'undefined' &&
                        !this.$route.params.item_id
                    ) {
                        this.$store.state.sidebar.AucGenre = null;
                        this.$store.state.sidebar.AucSubTypeID = null;
                    } else {
                        if (typeof this.$route.params.AucGenre !== 'undefined')
                            this.$store.state.sidebar.AucGenre = this.$route.params.AucGenre === 'empty' ? '' : this.$route.params.AucGenre;
                        if (typeof this.$route.params.AucSubTypeID !== 'undefined')
                            this.$store.state.sidebar.AucSubTypeID = this.$route.params.AucSubTypeID ? this.$route.params.AucSubTypeID : '';
                    }
                }
            },
        }
    }
</script>